export default {
	name: 'uni-number-box',
	props: {
		value: {
			type: Number,
			default: 1
		},
		index: {
			type: Number,
			default: 0
		},
		min: {
			type: Number,
			default: 0
		},
		max: {
			type: Number,
			default: 500
		},
		step: {
			type: Number,
			default: 1
		},
		disabled: {
			type: Boolean,
			default: false
		},
		focus: {
			type: Boolean,
			default: false
		},
		code: {
			type: String,
			default: ''
		},
		type: {
			type: String,
			default: ''
		}
	},
	data() {
		return {
			inputValue: this.value,
			status: 0
		}
	},
	computed: {
		disableSubtract() {
			return this.inputValue <= this.min
		},
		disableAdd() {
			return this.inputValue >= this.max
		}
	},
	watch: {
		value(val) {
			this.inputValue = val;
		},
		inputValue(val) {
			const data = {
				number: val,
				index: this.index
			}
			this.$emit('change', data);
		}
	},
	methods: {
		// 编辑
		_calcValue(type) {
			this.status = type
			if (this.disabled) {
				return
			}
			const scale = this._getDecimalScale()
			let value = this.inputValue * scale
			let step = this.step * scale
			if (type === 'subtract') {
				value -= step
			} else if (type === 'add') {
				value += step
			}
			if (value < this.min || value > this.max) {
				return
			}
			if (this.type == "cart") {
				let content = {
					item_no: this.code,
					num: value / scale
				}
				console.log(content)
				this.$ajax.changenum(content).then(res => {
					if (res.result == "success") {
						this.inputValue = value / scale;
					} else {
						this.$api.msg(res.msg);
					}
				})
			} else {
				this.inputValue = value / scale;
			}

		},
		_getDecimalScale() {
			let scale = 1
			// 浮点型
			if (~~this.step !== this.step) {
				scale = Math.pow(10, (this.step + '').split('.')[1].length)
			}
			return scale
		},
		// input
		_onBlur(event) {
			console.log(event)
			let value = event.detail.value
			if (!value) {
				this.inputValue = 0
				return
			}
			value = +value;
			if (value > this.max) {
				value = this.max
			} else if (value < this.min) {
				value = this.min
			}
			this.inputValue = value
		}
	}
}
